import { NextResponse } from "next/server";
import nodemailer from "nodemailer";

export async function POST(req: Request) {
  try {
    const { email } = await req.json();

    // Generate 6-digit OTP
    const otp = Math.floor(100000 + Math.random() * 900000).toString();

    // Create storage if missing
    if (!global.otpStore) global.otpStore = {};

    // Save OTP with 5-min expiration
    global.otpStore[email] = {
      otp,
      expires: Date.now() + 5 * 60 * 1000,
    };

    // Setup Gmail SMTP transport
    const transporter = nodemailer.createTransport({
      service: "gmail",
      auth: {
        user: process.env.GMAIL_USER,
        pass: process.env.GMAIL_PASS, // App password
      },
    });

    // Send email
    await transporter.sendMail({
      from: process.env.GMAIL_USER,
      to: email,
      subject: "Your OTP Code - ProfConnect",
      text: `Your OTP is: ${otp}\n\nThis OTP is valid for 5 minutes.`,
    });

    return NextResponse.json({ success: true });
  } catch (error) {
    console.error("Send OTP Error:", error);
    return NextResponse.json({ success: false });
  }
}
